export class SpeciesData {

  speciesName: string = '';

  /* Fields for daily catch reports */

  kept: number = 0;

  released: number = 0;

  /* Fields for offload catch reports */

  offloadWeight: number = 0; // for seine reports only

  offloadPieces: number = 0; 

  constructor(speciesName: string) {
    this.speciesName = speciesName;
  }

  public toString(): string {
    return this.speciesName;
  }

  public isValid(): boolean {
    return !!this.speciesName;
  }

}
