import { Component, ViewChild } from '@angular/core';
import { AuthService, User } from '@auth0/auth0-angular';
import { UserData } from '../../data-models/user-models/user-data.model';
import { FormControl, UntypedFormGroup, Validators, ValidationErrors, AbstractControl } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Tel, TelInputComponent } from '../../tel-input/tel-input.component';
import { generalValidator } from '../../data-models/validator.model';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-contact-info-dialog',
  templateUrl: './contact-info-dialog.component.html',
  styleUrls: ['./contact-info-dialog.component.css']
})

export class ContactInfoDialogComponent {

  @ViewChild('telInput') telInput!: TelInputComponent; // telephone input

  // get the current user details
  userDetails: UserData = new UserData('', '');

  // show error message when field is invalid
  emailError: string = 'Required';
  postalError: string = 'Required';

  // regular expression for Canadian postal code validation
  // from https://stackoverflow.com/questions/15774555/efficient-regex-for-canadian-postal-code-function
  postalRegex: RegExp = /^[ABCEGHJ-NPRSTVXY]\d[ABCEGHJ-NPRSTV-Z][ -]?\d[ABCEGHJ-NPRSTV-Z]\d$/i;

  profileForm: UntypedFormGroup = new UntypedFormGroup({
    contactEmail: new FormControl<string>('', [Validators.required, Validators.email]),
    address: new FormControl<string>('', [Validators.required]),
    city: new FormControl<string>('', [Validators.required]),
    postalCode: new FormControl<string>('', [Validators.required, Validators.pattern(this.postalRegex)]),
    phoneNum: new FormControl<Tel>(new Tel('', '', ''), [Validators.required, generalValidator()]),
  });

  constructor(
    private dialogRef: MatDialogRef<ContactInfoDialogComponent>,
    private auth: AuthService,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  ngOnInit(): void {

    let contactEmail: AbstractControl<any, any> | null = this.profileForm.get('contactEmail');
    let postalCode: AbstractControl<any, any> | null = this.profileForm.get('postalCode');

    // contact email validation
    contactEmail?.valueChanges.subscribe(() => {
      let contactEmailErrors: ValidationErrors | null | undefined = contactEmail?.errors;
      if (contactEmailErrors?.required) {
        this.emailError = 'Required';
      }
      else if (contactEmailErrors?.email) {
        this.emailError = 'Invalid format';
      }
      else {
        this.emailError = '';
      }
    });

    // postal code validation
    postalCode?.valueChanges.subscribe(() => {
      let postalCodeErrors: ValidationErrors | null | undefined = postalCode?.errors;
      if (postalCodeErrors?.required) {
        this.postalError = 'Required';
      }
      else if (postalCodeErrors?.pattern) {
        this.postalError = 'Invalid format';
      }
      else {
        this.postalError = '';
      }
    });

  }

  initialize() {

    this.profileForm.patchValue({
      contactEmail: this.userDetails.contactEmail,
      address: this.userDetails.address,
      city: this.userDetails.city,
      postalCode: this.userDetails.postalCode,
      phoneNum: this.userDetails.phoneNum
    });

  }

  phoneNumValid(): boolean {
    let phoneNum: Tel = this.profileForm.get('phoneNum')?.value;
    let area: string = phoneNum.area;
    let exchange: string = phoneNum.exchange;
    let subscriber: string = phoneNum.subscriber;
    return !!area && !!exchange && !!subscriber;
  }

  updateContactInfo(update: boolean): void {
    if (update) {
      this.attemptUpdate();
    }
    else {
      this.dialogRef.close('false');  
    }   
  }

  attemptUpdate(): void {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      this.toastr.error("Cannot update while offline", "Profile update failed");
      this.dialogRef.close('false');
    }

    // verify phone number
    this.telInput.touched = true;
    this.telInput.stateChanges.next();
    this.profileForm.get('phoneNum')?.updateValueAndValidity();

    // check profile field entry validity
    if (this.profileForm.valid && this.phoneNumValid()) {

      // navigate to the home screen
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails.contactEmail = this.profileForm.get('contactEmail')?.value;
                  this.userDetails.address = this.profileForm.get('address')?.value;
                  this.userDetails.city = this.profileForm.get('city')?.value;
                  this.userDetails.postalCode = this.profileForm.get('postalCode')?.value;
                  this.userDetails.phoneNum = this.profileForm.get('phoneNum')?.value;
                  LocalStorageService.saveUser(this.userDetails).then(() => {
                    console.log("User data saved to PouchDB");
                    this.dialogRef.close('true');
                  });
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }
    else {
      this.toastr.error("Missing information", "Profile update failed");
    }
  }

}
