import { Component, ViewChild } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators, AbstractControl } from '@angular/forms';
import { AuthService, User } from '@auth0/auth0-angular';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Trip } from '../../data-models/trip-models/trip.model';
import { SpeciesData } from '../../data-models/catch-table-models/species-data.model';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatTable } from '@angular/material/table';
import { AddSpeciesDialogComponent } from '../../dialogs/add-species-dialog/add-species-dialog.component';
import { TableRules } from 'src/app/data-models/catch-table-models/table-rules.model';
import { OffloadReport } from 'src/app/data-models/report-models/offload-report.model';
import { OffloadTableDialogComponent } from 'src/app/dialogs/offload-table-dialog/offload-table-dialog.component';


@Component({
  selector: 'app-offload-report',
  templateUrl: './offload-report.component.html',
  styleUrls: ['./offload-report.component.css']
})
export class OffloadReportComponent {

  // table data
  @ViewChild(MatTable) table!: MatTable<SpeciesData>; // for updating the table
  offloadTable: TableRules = new TableRules(); // used in add species dialog
  tableDataSource: SpeciesData[] = []; // all species data currently in the table
  displayedColumns: string[] = []; // columns currently being displayed in the table
  weightUnitOptions: string[] = ['Lbs', 'Kgs']; //hardcoded values to be used in the form

  // data preambles
  responseOptions: string[] = ['Yes', 'No']; //hardcoded values to be used in the form
  userDetails: UserData = new UserData('', '');
  unfilteredFishList: SpeciesData[] = []; // temporary array to store species
  fishList: SpeciesData[] = []; //filtered list of species data, will comprise the user's look-up list

  currTrip: Trip = new Trip();
  offloadReport: OffloadReport = new OffloadReport();

  offloadForm: UntypedFormGroup = new UntypedFormGroup({
    fishSlipIssued: new FormControl<string>('', [Validators.required]), // page 2
    fishSlipNumber: new FormControl<string>(''), // page 2 (conditionally validated)
    weightUnits: new FormControl<string>(''), // page 2 (always auto-selected so no need for validation)
  });
  offloadDate: Date = new Date(); // page 1
  offloadTime: { hour: number, minute: number } = { hour: 0, minute: 0 }; // page 1

  // page controls
  numPages: number = 0;
  currPage: number = 0;
  spinnerProgress: number = 0;
  spinnerIncrement: number = 0;


  constructor(
    private dialog: MatDialog,
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  ngOnInit(): void {
    this.onFishSlipChange();
  }

  onFishSlipChange(): void {
    this.offloadForm.get('fishSlipIssued')?.valueChanges.subscribe((value: string) => {
      const fishSlipControl: AbstractControl<any, any> | null = this.offloadForm.get('fishSlipNumber');

      if(value === 'Yes'){
        fishSlipControl?.setValidators([Validators.required]);
      }else{
        fishSlipControl?.clearValidators();
      }
      fishSlipControl?.updateValueAndValidity();
    })
  }

  initialize(): void {

    // get all required data
    this.currTrip = this.userDetails.currTrip;
    this.unfilteredFishList = LocalStorageService.getSpeciesList();
    this.setTableRules(); 
    this.filterSpeciesData(); // filter species list available to the user

    // initialize the table 
    this.offloadForm.patchValue({ weightUnits: 'Lbs' }); // automatically set to lbs (most common unit)
    this.setDisplayedColumns();

    // set the start time to the current time
    this.offloadTime = {
      hour: this.offloadDate.getHours(),
      minute: this.offloadDate.getMinutes()
    };

    this.numPages = 3;
    this.spinnerIncrement = 100 / this.numPages;
    this.toNextPage();

  }

  offloadTableDialog(): void {

    const dialogRef: MatDialogRef<OffloadTableDialogComponent, any> = this.dialog.open(OffloadTableDialogComponent, {
      width: '500px',
      height: '550px',
      data: { catchReports: this.userDetails.currTrip.dailyCatchReports, tableData: this.tableDataSource }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {
        this.tableDataSource = result;
        this.updateTable();
      }
    });

  }

  // table rules will be the same as they are for the current gear's Daily Catch Report with a couple differences:
    // tableType will instead be 'offload-catch' and containsSpecies list will be cleared since we are getting that info from the daily catch reports
  setTableRules(): void {
    this.offloadTable = LocalStorageService.getTableRules(this.currTrip.gear.name);
    this.offloadTable.tableType = 'offload-catch';
    this.offloadTable.containsSpecies.length = 0; // clear contains list
  }

  toNextPage(): void {

    if (this.currPage <= this.numPages) {
      if(this.currPage === 1) {
        this.setDateTime();
      }
      this.currPage++;
      this.spinnerProgress += this.spinnerIncrement;
    } else {
      this.sendToDFO();
    }
  
  }

  toPreviousPage(): void {
    if(this.currPage === 1) {
      this.toHome();
    }
    this.currPage--;
    this.spinnerProgress -= this.spinnerIncrement;
  }

  setDateTime(): void {

    let startHours: number = this.offloadTime.hour;
    let startMinutes: number = this.offloadTime.minute;

    this.offloadDate.setHours(startHours ? startHours : this.offloadDate.getHours());
    this.offloadDate.setMinutes(startMinutes ? startMinutes : this.offloadDate.getMinutes());
    this.offloadDate.setSeconds(0);
    this.offloadDate.setMilliseconds(0);

  }

  setDisplayedColumns(): void {
    if(this.getCurrentGearType() === 'Seine'){
      this.displayedColumns = ['speciesName', 'weight', 'pieces'];
    } else { // will be Gill Net in this case
      this.displayedColumns = ['speciesName', 'pieces'];
    }
  
  }

  fishSlipVal(): string { // returns the the response to the fish slip issues question
    return this.offloadForm.get('fishSlipIssued')?.value;
  }

  getWeightUnits(): string {
    return this.offloadForm.get('weightUnits')?.value;
  }

  getCurrentGearType(): string {
    return this.currTrip.gear.name;
  }

  filterSpeciesData(): void {  // filters the species list accessible to the user in the add species dialog
    for (let species of this.unfilteredFishList) {
      let excludeFlag: boolean = false;
      for (let tableSpecies of this.tableDataSource) {
        if (species.speciesName === tableSpecies.speciesName) {
          excludeFlag = true; // we only want to add species to the list that are not already in the table
        }
      }
      if (!excludeFlag) { // if the species is not already in the table
        if(!this.offloadTable.excludesSpecies.includes(species.speciesName) && // only add the species if it is not excluded from in the gear type
        !this.offloadTable.releaseOnlySpecies.includes(species.speciesName)) { // only add the species if it is not release-only
          this.fishList.push(species);
        }
      } 
      excludeFlag = false; // reset flag
    }
  }

   // add to, edit or delete from species catch list
   addSpeciesDialog(index: number = -1): void {

    // editing or deleting a species
    if (index > -1) {
          this.editSpecies(this.tableDataSource, index);
      }

    // adding a species
    else {
          this.addSpecies(this.tableDataSource);
      }

  }

  // add new species to the catch
  addSpecies(fishListDetails: SpeciesData[]): void {

    const dialogRef: MatDialogRef<AddSpeciesDialogComponent, any> = this.dialog.open(AddSpeciesDialogComponent, {
      width: '500px',
      height: '450px',
      data: { identifier: 'add', species: new SpeciesData(''), currentFishList: fishListDetails, 
      filteredFishList: this.fishList, tableInfo: this.offloadTable }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // adding a species to the catch
        if (result !== 'delete') {
          fishListDetails.push(this.getNewSpecies(result));
        }

        // update the table
        this.updateTable();
      }
    });

  }

   // edit or delete a species from the catch
   editSpecies(fishListDetails: SpeciesData[], index: number): void {

    const dialogRef: MatDialogRef<AddSpeciesDialogComponent, any> = this.dialog.open(AddSpeciesDialogComponent, {
      width: '500px',
      height: '450px',
      data: { identifier: 'edit', species: this.tableDataSource[index], currentFishList: fishListDetails, 
      filteredFishList: this.fishList, tableInfo: this.offloadTable  }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // editing a species in the catch
        if (result !== 'delete') {
          fishListDetails[index] = this.getNewSpecies(result);
        }

        // delete a species in the catch
        else {
          fishListDetails.splice(index, 1);
        }

        // update the table
        this.updateTable();

      }
    });

  }

  determineCellStyling(value: number): string {
    if(value>0){
      return 'bolded-cell';
    } 
    return 'faded-cell';
  }

  // get new species data from result
  getNewSpecies(result: SpeciesData): SpeciesData {

    let weight: number = result.offloadWeight ? +result.offloadWeight : 0;
    let pieces: number = result.offloadPieces ? +result.offloadPieces : 0;
    let newSpeciesData: SpeciesData = new SpeciesData(result.speciesName);
    newSpeciesData.offloadWeight = weight;
    newSpeciesData.offloadPieces = pieces;

    return newSpeciesData;

  }

  calculateTotalFish(type: string): number {

    let totalFish = 0;

    if(type === 'weight'){
      for(var i=0; i<this.tableDataSource.length; i++){
        totalFish += this.tableDataSource[i].offloadWeight;
      }
    }else if(type === 'pieces'){
      for(var i=0; i<this.tableDataSource.length; i++){
        totalFish += this.tableDataSource[i].offloadPieces;
      }
    }
    return totalFish;
  }

   // update the specified species catch table
   updateTable(): void {
    this.table.renderRows();
  }

  toHome(): void { 
    // navigate to the user's current homepage (may be offload-home or active-trip-home)
    this.router.navigate([this.userDetails.currHomepage]); 
  }

   // forbid field from entering non-decimal values
  forbidNonNumeric(controlName: string): void {
    let control: AbstractControl = this.offloadForm.get(controlName)!;
    control.setValue(control.value.replace(/[^0-9]/g, ''));
  }

  determineSpeciesData(): SpeciesData[] {

    let tempFishArray: SpeciesData[] = [];

    for(let fish of this.tableDataSource) {
      if (fish.offloadWeight > 0 || fish.offloadPieces > 0) {
        tempFishArray.push(fish);
      }
    }
    return tempFishArray;

  }

  saveReportData(): void {

    // copy relevant data to offload report object
    this.offloadReport.offloadDate = this.offloadDate;
    this.offloadReport.fishSlip = this.offloadForm.get('fishSlipIssued')?.value;
    this.offloadReport.fishSlipNumber = this.offloadForm.get('fishSlipNumber')?.value;
    this.offloadReport.speciesDataList = this.determineSpeciesData();
    this.offloadReport.catchWeightUnits = this.offloadForm.get('weightUnits')?.value;
    this.offloadReport.submissionDate = new Date(); // get current date/time

    // push the report to currTrip
    this.currTrip.offloadReports.push(this.offloadReport);

    // save to PouchDB
    this.userDetails.currTrip = this.currTrip;
    LocalStorageService.saveUser(this.userDetails).then(() => {
      console.log("User data saved to PouchDB");
      this.router.navigate(['submitted-report'], { state: { name: 'Offload Catch Report' } });
    });
  }

  sendToDFO(): void {

    if (this.offloadForm.valid) {
      this.saveReportData();
    }
    else {
      this.toastr.error("Not all required fields filled", "Invalid form");
    }

  }

}
