import { AbstractControl, ValidatorFn, ValidationErrors } from '@angular/forms';

export class Validator {

}

// validator for general values
export function generalValidator(): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    return !!control.value && control.valid ? null : { invalid: true };
  }
}

// choosing a species from a species list
export function speciesValidator(speciesList: string[]): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    for (let species of speciesList) {
      if (control.value.toLowerCase() === species.toLowerCase()) {
        return null;
      }
    }
    return { value: { error: 'Invalid species' } };
  }
}
