export class StartReport {

    startDate: Date = new Date(); // same as start date in trip model

    managementArea: string = ''; 

    comments: string = ''; 

    confirmationNumber: string = ''; // not currently used, will be the same as the trip ID

    submissionDate: Date = new Date(); // not currently used, but app will eventually populate dynamically

    /* Flags for programmatical use */

    isComplete: boolean = false; 

    isInProgress: boolean = false; 

    isTransmitted: boolean = false; // flag that tells us if the report has been sent to DFO - not currently used

    constructor() {

    }

    public isValid(): boolean {
        return this.isComplete && this.managementArea != '';
    }

    public toString(): string {
        return 'Start Date: ' + this.startDate + '; Confirmation Number: ' + this.confirmationNumber;
    }


}
