import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { VesselData } from '../../data-models/user-models/vessel-data.model';

@Component({
  selector: 'app-add-vessel-dialog',
  templateUrl: './add-vessel-dialog.component.html',
  styleUrls: ['./add-vessel-dialog.component.css']
})
export class AddVesselDialogComponent implements OnInit {

  dialogReturn: VesselData = new VesselData('', '');

  vesselForm: UntypedFormGroup = new UntypedFormGroup({
    vesselName: new FormControl<string>('', [Validators.required, Validators.pattern('^[A-z0-9 ]+$')]),
    vesselNumber: new FormControl<string>('', [Validators.required, Validators.pattern('^([0-9]{6}|[A-z0-9]{7,8})$')])
  })
  
  constructor(public dialogRef: MatDialogRef<AddVesselDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: VesselData) { // at the moment we don't need to inject data (leave in for the possibility of editing vessels later)

  }


  ngOnInit(): void {
    if (this.data) {
      this.vesselForm.patchValue({ // this doesn't do anything at the moment - but may need later for editing vessels
        vesselName: this.data.vesselName,
        vesselNumber: this.data.vesselCoastNumber
      });
    }
  }

  addVessel() {
    if (this.vesselForm.valid) {
      this.dialogRef.close(this.vesselForm.value);
    } else {
      this.vesselForm.markAllAsTouched();
    }    
  }

  deleteVessel() {
    this.dialogRef.close('delete');
  }

}
