import { Component } from '@angular/core';
import { AuthService, User } from '@auth0/auth0-angular';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { ToastrService } from 'ngx-toastr';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Gear } from '../../data-models/trip-models/gear-models/gear.model';
import { VesselData } from '../../data-models/user-models/vessel-data.model';
import { AddVesselDialogComponent } from '../../dialogs/add-vessel-dialog/add-vessel-dialog.component';
import { Net } from '../../data-models/trip-models/gear-models/net.model';
import { AddNetDialogComponent } from '../../dialogs/add-net-dialog/add-net-dialog.component';
import { ContactInfoDialogComponent } from 'src/app/dialogs/contact-info-dialog/contact-info-dialog.component';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent {

  // get the current user details
  userDetails: UserData = new UserData('', '');

  // read-only fields
  firstName: string = '';
  lastName: string = '';
  username: string = '';
  logbooks: Gear[] = [];

  // vessel and net data
  vessels: VesselData[] = [];
  containsGillNet: boolean = false;
  nets: Net[] = [];

  constructor(
    private dialog: MatDialog,
    private auth: AuthService,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }


  // set page values
  initialize(): void {

    this.firstName = this.userDetails.firstName;
    this.lastName = this.userDetails.lastName;
    this.username = this.userDetails.userEmail;
    this.logbooks = this.userDetails.gears;

    this.vessels = this.userDetails.vessels;

    for (let logbook of this.logbooks) {
      if (logbook.name === 'Gill Net') {
        this.containsGillNet = true;
        this.nets = this.userDetails.nets;
        break;
      }
    }

  }

  // edit contact info
  contactInfoDialog(): void {

    const dialogRef: MatDialogRef<ContactInfoDialogComponent, any> = this.dialog.open(ContactInfoDialogComponent, {
      width: '500px',
      height: '550px'
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // update selected vessel as new vessel added then go to next screen
        if (result !== 'false') {

          if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
            this.userDetails = LocalStorageService.getUserByUsername(this.userDetails.userEmail);
          }

          else {
            this.auth.isAuthenticated$.subscribe((authd: boolean) => {
              if (authd) {
                this.auth.user$.subscribe((user: User | null | undefined) => {
                  if (user) {
                    let id: string | undefined = user.sub;
                    let email: string | undefined = user.email;
                    if (id && email) {
                      LocalStorageService.getUser(id, email).then(() => {
                        this.userDetails = LocalStorageService.currUser;
                        this.toastr.success("Profile Information Updated!", "Success");
                      });
                    }
                    else {
                      this.toastr.error("Please authenticate again", "ID or Email not defined");
                      this.authenticationService.logout();
                    }
                  }
                  else {
                    this.toastr.error("Please authenticate again", "Cannot get user data");
                    this.authenticationService.logout();
                  }
                });
              }
              else {
                this.toastr.error("Please authenticate again", "Not authenticated");
                this.authenticationService.logout();
              }
            });
          }
        }
      }
    });

  }

  // add new vessel to user profile
  addVesselDialog(): void {

    const dialogRef: MatDialogRef<AddVesselDialogComponent, any> = this.dialog.open(AddVesselDialogComponent, {
      width: '500px',
      height: '400px'
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // update selected vessel as new vessel added then go to next screen
        if (result !== 'delete') {
          let newVessel: VesselData = new VesselData(result.vesselName, result.vesselNumber, true);
          this.userDetails.vessels.push(newVessel);
          LocalStorageService.saveUser(this.userDetails).then(() => {
            this.toastr.success("New vessel added!", "Success");
          });
        }

      }
    });
  }

  // add new net to user profile
  addNetDialog(): void {

    const dialogRef: MatDialogRef<AddNetDialogComponent, any> = this.dialog.open(AddNetDialogComponent, {
      width: '500px',
      height: '550px'
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // update selected net as new net added then go to next screen
        if (result !== 'delete') {
          let newNet: Net = new Net(result.netType, result.netName, result.netLength, result.netDepth, result.netUnits,
            result.hangRatio, result.meshSize, result.meshUnits, result.numMeshes, result.numStrands, true);
          this.userDetails.nets.push(newNet);
          LocalStorageService.saveUser(this.userDetails).then(() => {
            this.toastr.success("New net added!", "Success");
          });
        }

      }
    });
  }

}
