import { Component, Inject } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators, AbstractControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LocalStorageService } from '../../services/local-storage.service';
import { Net } from 'src/app/data-models/trip-models/gear-models/net.model';

@Component({
  selector: 'app-add-net-dialog',
  templateUrl: './add-net-dialog.component.html',
  styleUrls: ['./add-net-dialog.component.css']
})
export class AddNetDialogComponent {

  //data preambles
  netTypes: string[] = []; // holds net type values
  unitTypes: string[] = ['cm', 'in', 'mm']; //hardcoded values to be used in the form

  dialogReturn: Net = new Net('', '', '', '', '', '', '', '', '');

  netForm: UntypedFormGroup = new UntypedFormGroup({
    netType: new FormControl<string>('', [Validators.required]),
    numStrands: new FormControl<string>(''), // conditional validation
    netName: new FormControl<string>('', [Validators.required]), 
    netLength: new FormControl<string>('', [Validators.required]), 
    netDepth: new FormControl<string>('', [Validators.required]), 
    netUnits: new FormControl<string>('', [Validators.required]), 
    hangRatio: new FormControl<string>('', [Validators.required]), 
    meshSize: new FormControl<string>('', [Validators.required]), 
    meshUnits: new FormControl<string>('', [Validators.required]), 
    numMeshes: new FormControl<string>('', [Validators.required]) 
  })
  
  constructor(
    public dialogRef: MatDialogRef<AddNetDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Net) { // don't need injection right now, but leave in for the future possibility of editing the net

       this.netTypes = LocalStorageService.getNetTypes(); // retrieve net types

  }

  netTypeVal(): string { // returns the net type the user has selected
    return this.netForm.get('netType')?.value;
  }


  ngOnInit(): void {
    this.onNetTypeChange();
  }

  onNetTypeChange(): void {
    this.netForm.get('netType')?.valueChanges.subscribe((value: string) => {
      const strandControl: AbstractControl<any, any> | null = this.netForm.get('numStrands');

      if(value === 'Alaska Twist'){
        strandControl?.setValidators([Validators.required]);
      }else{
        strandControl?.clearValidators();
      }
      strandControl?.updateValueAndValidity();
    })
  }

  addNet() { 
    if (this.netForm.valid) {
      this.clearInputs();
      this.dialogRef.close(this.netForm.value); // return the filled-in values of the form field
    } else {
      this.netForm.markAllAsTouched();
    }    
  }


  deleteNet(): void { 
    this.dialogRef.close('delete');
  }

  // reset any dead form inputs
  clearInputs(): void {
    if(this.netTypeVal() != 'Alaska Twist'){ // if other gear type, clear any possible input in numStrands
      this.netForm.controls['numStrands'].reset();
    }
  }

  // forbid field from entering non-decimal values
  forbidNonNumeric(controlName: string): void {
    let control: AbstractControl = this.netForm.get(controlName)!;
    control.setValue(control.value.replace(/[^0-9]/g, ''));
  }

}
