import { Net } from "../trip-models/gear-models/net.model";
import { SpeciesData } from "../catch-table-models/species-data.model";

export class DailyCatchReport {

    netUsed: Net = new Net('', '', '', '', '', '', '', '', ''); // unique to Gill Net

    dateFished: Date = new Date();

    hoursFished: string = '';

    managementArea: string = '';

    subArea: string = '';

    numSets: string = ''; // unique to Gill Net & Seine

    frozenOrIced: string = ''; // unique to Troll

    speciesDataList: SpeciesData[] = []; // catch data

    nonFish: string = '';

    comments: string = ''; // only required if "Yes" answered for nonFish

    confirmationNumber: string = ''; // daily catch report confirmation number (not being used yet)

    submissionDate: Date = new Date(); 

    /* Flags for programmatical use */

    isTransmitted: boolean = false; // flag that tells us if the report has been sent to DFO - not currently used

    constructor() {

    }

    public toString(): string {
        return 'Date: ' + this.dateFished + '; Daily Catch Report Confirmation Number: ' + this.confirmationNumber;
      }

}
