import { Component } from '@angular/core';
import { AuthService, User } from '@auth0/auth0-angular';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { UserData } from 'src/app/data-models/user-models/user-data.model';
import { Trip } from 'src/app/data-models/trip-models/trip.model';
import { DatePipe } from '@angular/common';
import { Gear } from 'src/app/data-models/trip-models/gear-models/gear.model';
import { LicenseArea } from 'src/app/data-models/trip-models/license-area.model';
import { VesselData } from 'src/app/data-models/user-models/vessel-data.model';

@Component({
  selector: 'app-records-page',
  templateUrl: './records-page.component.html',
  styleUrls: ['./records-page.component.css']
})
export class RecordsPageComponent {

   //data preambles
   userDetails: UserData = new UserData('', '');
   currTrip: Trip = new Trip();

   //filter menu
   gears: Gear[] = [];
   licenseAreas: LicenseArea[] = [];
   vessels: VesselData[] = [];
   selectedGearCount: number = 0;
   selectedLicenseAreaCount: number = 0;
   selectedVesselCount: number = 0;

  constructor( // same constructor as original homepage
    private auth: AuthService,
    private datePipe: DatePipe,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  formatDate(date: Date): string {
    let formattedDate = this.datePipe.transform(date, 'shortDate');
    if (formattedDate){
      return formattedDate;
    }
    this.toastr.error("Could not format date", "Date format error");
    return '';
  }

  initialize(): void {

    // get current fishing report information
    this.currTrip = this.userDetails.currTrip;

    // get data for filter list
    this.makeGearList(this.userDetails.gears);
    this.makeLicenseAreaList(LocalStorageService.getLicenseAreas());
    this.makeVesselList(this.userDetails.vessels);

  }

  makeVesselList(vesselList: VesselData[]) {
    for(let vessel of vesselList) {
      let currVessel: VesselData = new VesselData();
      currVessel.vesselName = vessel.vesselName;
      currVessel.vesselCoastNumber = vessel.vesselCoastNumber;
      this.vessels.push(currVessel);
    }
  }

  makeGearList(gearList: Gear[]): void {
    for(let gear of gearList) {
     let currGear: Gear = new Gear();
     currGear.name = gear.name;
     currGear.logbookID = gear.logbookID;
     this.gears.push(currGear);
    }
  }

  makeLicenseAreaList(areas: LicenseArea[]): void {
    for(let area of areas) {
      let currArea: LicenseArea = new LicenseArea();
      currArea.licenseName = area.licenseName;
      currArea.licenseLocation = area.licenseLocation;
      this.licenseAreas.push(currArea);
     }
  }


  getDate(date: Date): Date {
    return new Date(date);
  }

  determineButtonStyling(): string {

    if (this.filtersApplied()) { // filter is being used
      return "filter-tool-selected";
    }
    return "filter-tool" // filter is not being used
  }

  filtersApplied(): boolean {
    return this.selectedGearCount > 0 || this.selectedLicenseAreaCount > 0 || this.selectedVesselCount > 0;
  }


  /* Credit to: 
  https://stackblitz.com/edit/multi-select-mat-menu?file=src%2Fapp%2Fmenu-multiselect-example.html,src%2Fapp%2Fmenu-multiselect-example.ts
  for selectItem and updateSelectedItems */
 
 selectItem($event: any, item: any): void {
    // prevent menu from closing
    $event.stopPropagation();
    $event.preventDefault();
    // toggle selected state on clicked item
    item.isChecked = !item.isChecked;
    this.updateSelectedItems();
 }

 updateSelectedItems(): void {
    this.selectedGearCount = this.gears.filter((g: Gear) => g.isChecked).length;
    this.selectedLicenseAreaCount = this.licenseAreas.filter((l: LicenseArea) => l.isChecked).length;
    this.selectedVesselCount = this.vessels.filter((v: VesselData) => v.isChecked).length;
 }

 determineTripVisibility(trip: Trip): boolean {

  let checkedGears: Gear[] = this.gears.filter((g: Gear) => g.isChecked);
  let checkedAreas: LicenseArea[] = this.licenseAreas.filter((l: LicenseArea) => l.isChecked);
  let checkedVessels: VesselData[] = this.vessels.filter((v: VesselData) => v.isChecked);

  let gearFlag: boolean = false;
  let licenseAreaFlag: boolean = false;
  let vesselFlag: boolean = false;

  if (!checkedGears.length) {
    checkedGears = this.gears;
  }
  if (!checkedAreas.length) {
    checkedAreas = this.licenseAreas;
  }
  if (!checkedVessels.length) {
    checkedVessels = this.vessels;
  }

  for(let gear of checkedGears) {
    if((trip.gear.logbookID === gear.logbookID)) {
      gearFlag = true;
      break;
    }
  } 
  if(!gearFlag) {
    return false;
  }

  for(let area of checkedAreas) {
    if((trip.licenseArea.licenseName === area.licenseName)) {
      licenseAreaFlag = true;
      break;
    }
  }
  if(!licenseAreaFlag) {
    return false;
  }

  for(let vessel of checkedVessels) {
    if((trip.vessel.vesselCoastNumber === vessel.vesselCoastNumber)) {
      vesselFlag = true;
      break;
    }
  }
  if(!vesselFlag) {
    return false;
  }

  return true; // match
  
 }

 clearGearFilter() : void {
   this.selectedGearCount = 0;
   let checkedGears: Gear[] = this.gears.filter((g: Gear) => g.isChecked);
   for(let gear of checkedGears) {
     gear.isChecked = false;
   }
 }

 clearAreaFilter() : void {
  this.selectedLicenseAreaCount = 0;
  let checkedAreas: LicenseArea[] = this.licenseAreas.filter((l: LicenseArea) => l.isChecked);
  for(let area of checkedAreas) {
    area.isChecked = false;
  }
}

clearVesselFilter() : void {
  this.selectedVesselCount = 0;
  let checkedVessels: VesselData[] = this.vessels.filter((v: VesselData) => v.isChecked);
  for(let vessel of checkedVessels) {
    vessel.isChecked = false;
  }
}

}
