import { VesselData } from './vessel-data.model';
import { Trip } from '../trip-models/trip.model';
import { Net } from '../trip-models/gear-models/net.model';
import { Gear } from '../trip-models/gear-models/gear.model';
import { Tel } from '../../tel-input/tel-input.component';

export class UserData {

  // Auth0 ID
  userID: string = '';

  // Email for signup
  userEmail: string = '';

  firstName: string = '';

  lastName: string = '';

  contactEmail: string = '';

  address: string = '';

  city: string = '';

  postalCode: string = '';

  phoneNum: Tel = new Tel('', '', '');

  // current homepage
  currHomepage: string = '';

  // saved user trips
  prevTrips: Trip[] = [];

  // saved user vessels
  vessels: VesselData[] = [];

  // current trip 
  currTrip: Trip = new Trip();

  // saved nets
  nets: Net[] = [];

  // purchased gear logbooks
  gears: Gear[] = [];

  constructor(id: string, email: string) {
    this.userID = id;
    this.userEmail = email;
  }

  public toString(): string {
    return this.userEmail + ', ' + this.userID;
  }

  public isValid(): boolean {
    return !!this.userID && !!this.userEmail;
  }

}
