export class LicenseArea {

  licenseID: string = '';

  licenseName: string = '';

  licenseLocation: string = '';

  isChecked: boolean = false;

  constructor(licenseName: string = '', licenseLocation: string = '', isChecked: boolean = false) {
    this.licenseName = licenseName;
    this.licenseLocation = licenseLocation;
    this.isChecked = isChecked;
  }

  public toString(): string {
    return this.licenseName + ' (' + this.licenseLocation + ')';
  }

  public isValid(): boolean {
    return !!this.licenseName && !!this.licenseLocation;
  }

  public isEqual(inputArea: LicenseArea): boolean {
    return inputArea.licenseName === this.licenseName;
  }

}
