import { Component, Inject } from '@angular/core';
import { AbstractControl, FormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { speciesValidator } from '../../data-models/validator.model';
import { SpeciesData } from '../../data-models/catch-table-models/species-data.model';
import { TableRules } from 'src/app/data-models/catch-table-models/table-rules.model';

@Component({
  selector: 'app-add-species-dialog',
  templateUrl: './add-species-dialog.component.html',
  styleUrls: ['./add-species-dialog.component.css']
})

export class AddSpeciesDialogComponent {

  // used to determine if the species name can be edited
  speciesIsReadOnly: boolean = false;

  // species lists
  stringList: string[] = []; // subset of "list" - has all species names (used only for validation)
  speciesList: string[] = []; 
  filteredSpecies: string[] = []; //what's already in table
  currentSpeciesList: string[] = [];

  // dialog title
  displayedTitle: string = '';

  // extracting data from data inject
  identifier: string = '';
  currentFishList: SpeciesData[] = [];
  filteredFishList: SpeciesData[] = [];
  tableInfo: TableRules = new TableRules('', '');

  // species details
  speciesForm: UntypedFormGroup = new UntypedFormGroup({
    speciesName: new FormControl<string>('', [Validators.required, speciesValidator(this.stringList)]),
    kept: new FormControl<string>(''),
    released: new FormControl<string>(''),
    offloadWeight: new FormControl<string>(''),
    offloadPieces: new FormControl<string>('')
  })

  constructor(
    public dialogRef: MatDialogRef<AddSpeciesDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { identifier: string, species: SpeciesData, currentFishList: SpeciesData[], 
      filteredFishList: SpeciesData[], tableInfo: TableRules }
  ) {

  }


  ngOnInit(): void {

    // if editing a species, autofill form with species details
    if (this.data != null) {

      this.identifier = this.data.identifier;
      this.currentFishList = this.data.currentFishList;
      this.filteredFishList = this.data.filteredFishList;
      this.tableInfo = this.data.tableInfo;

      if (this.data.species != null) {
        let speciesData: SpeciesData = this.data.species;
        this.speciesForm.patchValue({
          speciesName: speciesData.speciesName,
          kept: speciesData.kept,
          released: speciesData.released,
          offloadWeight: speciesData.offloadWeight,
          offloadPieces: speciesData.offloadPieces
        });
      }
    }

    // fill the species list available to the user
    this.fillSpecies();

    // set the dialog configuration
    this.setDialogConfig();

    // change the filtered species list whenever the input field changes
    this.speciesForm.get('speciesName')!.valueChanges.subscribe((speciesChange: string) => {
      this.filteredSpecies = [];
      for (let species of this.speciesList) {
        if (species.toLowerCase().includes(speciesChange.toLowerCase())) {
          this.filteredSpecies.push(species);
        }
      }
    });

  }

  // set the title of the dialog and read/write permissions of inputs
  setDialogConfig(): void {
     // set config based on whether the species is being added or edited
     switch (this.identifier) {
      case 'edit':
        this.displayedTitle = "Edit Species";
        this.speciesIsReadOnly = true;
        break;
      case 'add':
         this.displayedTitle = "Add Species";
         break;
    }
  }

  // fill up the species list based on what is not already in the table
  fillSpecies(): void { // list of species gets passed in here
    for(let species of this.currentFishList) { // get all species names that are already in the table
      this.currentSpeciesList.push(species.speciesName);
    } 
    for (let species of this.filteredFishList) {
      //only add the name to the list if it is not already in the table 
      if(!this.currentSpeciesList.includes(species.speciesName)) {
        this.speciesList.push(species.speciesName); 
      }
      this.stringList.push(species.speciesName); // fill the string list regardless
    }
    // fill the filtered list
    this.filteredSpecies = this.speciesList;
  }

  // check if entered species is a valid species
  checkSpecies(name: string): boolean {
    for (let species of this.filteredFishList) {
      if (species.speciesName.toLowerCase().includes(name.toLowerCase())) {
        this.speciesForm.patchValue({
          speciesName: species.speciesName,
        });
        return true;
      }
    }
    return false;
  }

  isReleaseOnly(name: string): boolean {
    if(this.tableInfo.releaseOnlySpecies.includes(name)){
      return true;
    }
    return false;
  }

  getCurrentFormSpecies(): string {
    return this.speciesForm.get('speciesName')?.value;
  }

  // add new species details
  addSpecies(): void {
    if (this.speciesForm.valid && this.checkSpecies(this.speciesForm.get('speciesName')!.value)) {
      this.clearInputs(); 
      this.dialogRef.close(this.speciesForm.value);
    } else {
      this.speciesForm.markAllAsTouched();
    }
  }

  // clear any possible dead form inputs
  clearInputs(): void {
    if(this.isReleaseOnly(this.getCurrentFormSpecies())){ // if species is release-only, clear any possible previous 'kept' input
      this.speciesForm.controls['kept'].reset(); // reset form value
    }
  }

  // delete current species details
  deleteSpecies(): void {
    this.dialogRef.close('delete');
  }

  // forbid field from entering non-decimal values
  forbidNonNumeric(controlName: string): void {
    let control: AbstractControl = this.speciesForm.get(controlName)!;
    control.setValue(control.value.replace(/[^0-9]/g, ''));
  }

}
