import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { ToastrService } from 'ngx-toastr';
import { Trip } from 'src/app/data-models/trip-models/trip.model';
import { UserData } from 'src/app/data-models/user-models/user-data.model';
import { DailyCatchReport } from 'src/app/data-models/report-models/daily-catch-report.model';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-no-trip-home',
  templateUrl: './no-trip-home.component.html',
  styleUrls: ['./no-trip-home.component.css']
})
export class NoTripHomeComponent {

  //data preambles
  userDetails: UserData = new UserData('', '');
  currTrip: Trip = new Trip();
  prevTrips: Trip[] = [];

  constructor( // same constructor as original homepage
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  saveDailyCatchReports(tempTrip: Trip): void {
    for(let report of this.currTrip.dailyCatchReports){
      let tempReport: DailyCatchReport = new DailyCatchReport();
      tempReport = report;
      tempTrip.dailyCatchReports.push(tempReport);
    }
  }

  saveOldReport(): void { 
    if(this.currTrip.isValid() && !this.currTrip.isSaved && this.currTrip.startFishingReport.isComplete) { // check if there is a trip to save
      console.log("Saving a new trip...");
      let tempTrip: Trip = new Trip();
      this.currTrip.isSaved = true;
      tempTrip.isSaved = this.currTrip.isSaved;
      tempTrip.gear = this.currTrip.gear;
      tempTrip.vessel = this.currTrip.vessel;
      tempTrip.licenseArea = this.currTrip.licenseArea;
      tempTrip.master = this.currTrip.master;
      tempTrip.fin = this.currTrip.fin;
      tempTrip.contactName = this.currTrip.contactName;
      tempTrip.phoneNum = this.currTrip.phoneNum;
      tempTrip.offloadNeeded = this.currTrip.offloadNeeded;
      tempTrip.startFishingReport = this.currTrip.startFishingReport;
      tempTrip.endFishingReport = this.currTrip.endFishingReport;
      tempTrip.cancelFishingReport = this.currTrip.cancelFishingReport;
      tempTrip.pauseFishingReports = this.currTrip.pauseFishingReports;
      this.saveDailyCatchReports(tempTrip);
      tempTrip.offloadReports = this.currTrip.offloadReports;
      this.prevTrips = this.userDetails.prevTrips;
      this.prevTrips.push(tempTrip);
      this.userDetails.currTrip = this.currTrip;
      this.userDetails.prevTrips = this.prevTrips;
      LocalStorageService.saveUser(this.userDetails).then(() => {
        console.log("Trip saved!");
      });
    }
  }

  initialize(): void {
    // get current fishing report information
    this.currTrip = this.userDetails.currTrip;
    this.saveOldReport();
  }

  // go to start a fishing report
  toStartReport(): void {
    this.router.navigate(['start-report']);
  }

}
