import { NgModule, isDevMode } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { MaterialModule } from './material/material.module';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { ToastrModule } from 'ngx-toastr';
import { AppRoutingModule } from './app-routing.module';
import { AuthModule } from '@auth0/auth0-angular';
import { AppComponent } from './app.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { LoginComponent } from './authentication/login/login.component';
import { AddVesselDialogComponent } from './dialogs/add-vessel-dialog/add-vessel-dialog.component';
import { NgbTimepickerModule } from '@ng-bootstrap/ng-bootstrap';
import { AddSpeciesDialogComponent } from './dialogs/add-species-dialog/add-species-dialog.component';
import { OnlineStatusModule } from 'ngx-online-status';
import { SignUpComponent } from './authentication/sign-up/sign-up.component';
import { TelInputComponent } from './tel-input/tel-input.component';
import { environment as env } from '../environments/environment';
import { PauseReportComponent } from './reports/pause-report/pause-report.component';
import { CancelReportComponent } from './reports/cancel-report/cancel-report.component';
import { SubmittedReportComponent } from './reports/submitted-report/submitted-report.component';
import { StartReportComponent } from './reports/start-report/start-report.component';
import { EndReportComponent } from './reports/end-report/end-report.component';
import { GillNetReportComponent } from './catch/gill-net-report/gill-net-report.component';
import { AddNetDialogComponent } from './dialogs/add-net-dialog/add-net-dialog.component';
import { SeineReportComponent } from './catch/seine-report/seine-report.component';
import { TrollReportComponent } from './catch/troll-report/troll-report.component';
import { NoTripHomeComponent } from './home/no-trip-home/no-trip-home.component';
import { MenuComponent } from './menu/menu.component';
import { MatIconModule } from '@angular/material/icon';
import { ActiveTripHomeComponent } from './home/active-trip-home/active-trip-home.component';
import { OffloadHomeComponent } from './home/offload-home/offload-home.component';
import { OffloadReportComponent } from './reports/offload-report/offload-report.component';
import { RecordsPageComponent } from './menu-items/records-page/records-page.component';
import { DatePipe } from '@angular/common';
import { ProfileComponent } from './menu-items/profile/profile.component';
import { OffloadTableDialogComponent } from './dialogs/offload-table-dialog/offload-table-dialog.component';
import { ContactInfoDialogComponent } from './dialogs/contact-info-dialog/contact-info-dialog.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    AddVesselDialogComponent,
    AddSpeciesDialogComponent,
    SignUpComponent,
    TelInputComponent,
    PauseReportComponent,
    CancelReportComponent,
    SubmittedReportComponent,
    StartReportComponent,
    EndReportComponent,
    GillNetReportComponent,
    AddNetDialogComponent,
    SeineReportComponent,
    TrollReportComponent,
    NoTripHomeComponent,
    MenuComponent,
    ActiveTripHomeComponent,
    OffloadHomeComponent,
    OffloadReportComponent,
    ProfileComponent,
    RecordsPageComponent,
    OffloadTableDialogComponent,
    ContactInfoDialogComponent,
  ],
  imports: [
    BrowserModule,
    MaterialModule,
    MatIconModule,
    FormsModule,
    ReactiveFormsModule,
    OnlineStatusModule,
    ToastrModule.forRoot(),
    AppRoutingModule,
    AuthModule.forRoot({
      domain: env.auth0.domain,
      clientId: env.auth0.clientId,
      authorizationParams: {
        redirect_uri: env.auth0.redirect_uri,
        audience: env.auth0.audience
      },
      cacheLocation: 'localstorage'
    }),
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: !isDevMode(),
      // Register the ServiceWorker as soon as the application is stable
      // or after 30 seconds (whichever comes first).
      registrationStrategy: 'registerImmediately'
    }),
    BrowserAnimationsModule,
    NgbTimepickerModule
  ],
  providers: [DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule { }
