import { Component } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { AuthService, User } from '@auth0/auth0-angular';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { UserData } from '../../data-models/user-models/user-data.model';
import { Trip } from '../../data-models/trip-models/trip.model';
import { EndReport } from 'src/app/data-models/report-models/end-report.model';

@Component({
  selector: 'app-end-report',
  templateUrl: './end-report.component.html',
  styleUrls: ['./end-report.component.css']
})
export class EndReportComponent {

  // data preambles
  userDetails: UserData = new UserData('', '');
  currTrip: Trip = new Trip();
  endFishingReport: EndReport = new EndReport();

  // report data
  endForm: UntypedFormGroup = new UntypedFormGroup({
    managementArea: new FormControl<string>('', [Validators.required]), // page 1
    comments: new FormControl<string>('') // page 2
  });
  endDate: Date = new Date(); // page 1

  // arrival form for 
  arrivalForm: UntypedFormGroup = new UntypedFormGroup({
    site: new FormControl<string>('', [Validators.required]) // arrival page (page 4)
  });
  arrivalTime: { hour: number, minute: number } = { hour: 0, minute: 0 };

  // page to page
  numPages: number = 0;
  currPage: number = 0;
  spinnerProgress: number = 0;
  spinnerIncrement: number = 0;


  constructor(
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "ID or Email not defined");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  // initialize values
  initialize(): void {

    // get current fishing report
    this.currTrip = this.userDetails.currTrip;

    // set time to current time
    this.arrivalTime = {
      hour: this.endDate.getHours(),
      minute: this.endDate.getMinutes()
    };

    this.numPages = 2;
    this.spinnerIncrement = 100 / this.numPages;
    this.toNextPage();

  }

  // check current page before going to the next one
  toNextPage(): void {

    if (this.currPage <= this.numPages) {
      this.incrementPage();
    }
    else {
      this.sendToDFO();
    }

  }

  // forward one page
  incrementPage(): void {
    this.currPage++;
    this.spinnerProgress += this.spinnerIncrement;
  }

  // back one page
  toPreviousPage(): void {

    if (this.currPage === 1) {
      this.toHome();
      return;
    }

    this.currPage--;
    this.spinnerProgress -= this.spinnerIncrement;

  }

  // check if management area is invalid
  managementAreaInvalid(): boolean {
    return this.endForm.get('managementArea')?.errors?.required;
  }

  // save End Fishing Report and Trip Data
  saveReportData(): void {

    if (this.currTrip.gear.name === 'Troll' && this.currTrip.licenseArea.licenseName === 'G') {
      this.setDateTime(); // add arrival time to the date
      this.endFishingReport.arrivalSite = this.arrivalForm.get('site')?.value;
    }
    this.endFishingReport.endDate = this.endDate; // save end date to report
    this.endFishingReport.isComplete = true; // set submission flag
    this.endFishingReport.managementArea = this.endForm.get('managementArea')?.value;
    this.endFishingReport.comments = this.endForm.get('comments')?.value;
    this.endFishingReport.submissionDate = new Date(); // get current date/time
    this.currTrip.endFishingReport = this.endFishingReport;
    this.userDetails.currTrip = this.currTrip;
    LocalStorageService.saveUser(this.userDetails).then(() => {
      console.log("User data saved to PouchDB");
      this.toSubmittedReport();
    });
  }

  // send start fishing report to DFO
  sendToDFO(): void {

    // not on arrival page
    if (this.currPage < 4) {

      if (this.endForm.valid) {

        // arrival details for troll gear and license area G
        if (this.currTrip.startFishingReport.isComplete && this.currTrip.gear.name === 'Troll' && this.currTrip.licenseArea.licenseName === 'G') {

          // set arrival time to current time
          let newDate: Date = new Date();
          this.arrivalTime = {
            hour: newDate.getHours(),
            minute: newDate.getMinutes()
          };

          // go to arrival page
          this.currPage++;

        }
        else {
          this.saveReportData();
        }
      }
      else {
        this.toastr.error("Not all required fields filled", "Invalid form");
      }

    }

    // arrival page
    else {

      if (this.arrivalForm.valid) {
        this.saveReportData();
      }
      else {
        this.toastr.error("Not all required fields filled", "Invalid form");
      }

    }

  }

  updateHomepage(homeName: string): void {
    this.userDetails.currHomepage = homeName;
    LocalStorageService.saveUser(this.userDetails).then(() => {
      this.router.navigate(['submitted-report'], { state: { name: 'End Fishing Report' } });
    });
  }

  // update the start date hour and minute, only needed on 'Next'
  setDateTime(): void {

    let arrivalHours: number = this.arrivalTime.hour;
    let arrivalMinutes: number = this.arrivalTime.minute;

    this.endDate.setHours(arrivalHours ? arrivalHours : this.endDate.getHours());
    this.endDate.setMinutes(arrivalMinutes ? arrivalMinutes : this.endDate.getMinutes());
    this.endDate.setSeconds(0);
    this.endDate.setMilliseconds(0);

  }

  toSubmittedReport(): void {
    if (this.currTrip.offloadNeeded) {
      this.updateHomepage('offload-home');
    } else {
      this.updateHomepage('no-trip-home');
    }
  }

  // go back to the active trip home screen
  toHome(): void {
    this.router.navigate(['active-trip-home']);
  }

}
