import { SpeciesData } from "../catch-table-models/species-data.model";

export class OffloadReport {

    offloadDate: Date = new Date(); // date and time provided by the user

    fishSlip: string = ''; // yes/no answer

    fishSlipNumber: string = ''; // may not always be provided

    catchWeightUnits: string = ''; // only needed for Seine

    speciesDataList: SpeciesData[] = []; // offload data - list of species and their associated weight/pieces

    confirmationNumber: string = ''; // not currently used, will be the End Fishing Report Confirmation Number

    submissionDate: Date = new Date(); // not currently used, but app will eventually populate dynamically

    /* Flags for programmatical use */

    isTransmitted: boolean = false; // flag that tells us if the report has been sent to DFO - not currently used


    constructor() {
    }

    public toString(): string {
        return 'Offload Date: ' + this.offloadDate + '; Confirmation Number: ' + this.confirmationNumber;
    }

}
