import { Gear } from './gear-models/gear.model';
import { VesselData } from '../user-models/vessel-data.model';
import { LicenseArea } from './license-area.model';
import { Tel } from '../../tel-input/tel-input.component';
import { DailyCatchReport } from '../report-models/daily-catch-report.model';
import { StartReport } from '../report-models/start-report.model';
import { EndReport } from '../report-models/end-report.model';
import { PauseReport } from '../report-models/pause-report.model';
import { CancelReport } from '../report-models/cancel-report.model';
import { OffloadReport } from '../report-models/offload-report.model';

export class Trip {

  /* General Trip Data */

  gear: Gear = new Gear('', '');

  vessel: VesselData = new VesselData('', '');

  licenseArea: LicenseArea = new LicenseArea('', '');

  master: string = '';

  fin: string = '';

  contactName: string = ''; // contact name

  phoneNum: Tel = new Tel('', '', ''); // contact phone number

  tripID: string = '123456'; // hardcoded for now, will eventually generate unique ones

  /* Flags for programmatical use */

  offloadNeeded: boolean = false; // used to determine if the current trip may require an offload report

  isSaved: boolean = false; // used to determine whether the report has been saved to the user's prevTrips array

  /* Fishing Report Data */

  startFishingReport: StartReport = new StartReport();

  endFishingReport: EndReport = new EndReport();

  cancelFishingReport: CancelReport = new CancelReport();

  pauseFishingReports: PauseReport[] = []; // array of all pause reports submitted for the trip

  dailyCatchReports: DailyCatchReport[] = []; // array of all daily catch reports submitted for the trip

  offloadReports: OffloadReport[] = [];  // array of all offload reports submitted for the trip

  constructor() {

  }

  public toString(): string {
    return 'Gear: ' + this.gear + '; Vessel: ' + this.vessel + '; License Area: ' + this.licenseArea;
  }

  public isValid(): boolean {
    return this.gear.isValid() && this.vessel.isValid() && this.licenseArea.isValid();
  }

}
