export class EndReport {

    // if arrival details are required, this date will also contain an arrival time
    endDate: Date = new Date(); // will be the same as in the trip model

    managementArea: string = ''; 

    comments: string = ''; 

    confirmationNumber: string = ''; // not currently used, will be the End Fishing Report Confirmation Number

    submissionDate: Date = new Date(); // not currently used, but app will eventually populate dynamically

    /* Arrival Details - only needed for certain trips */

    arrivalSite: string = ''; 

    /* Flags for programmatical use */

    isComplete: boolean = false; // has this report been submitted?

    isTransmitted: boolean = false; // flag that tells us if the report has been sent to DFO - not currently used

    constructor() {

    }

    public toString(): string {
        return 'End Date: ' + this.endDate + '; Confirmation Number: ' + this.confirmationNumber;
    }

}
