import { Component, ViewChild } from '@angular/core';
import { FormControl, UntypedFormGroup, Validators, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatTable } from '@angular/material/table';
import { AuthService, User } from '@auth0/auth0-angular';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';
import { UserData } from '../../data-models/user-models/user-data.model';
import { SpeciesData } from '../../data-models/catch-table-models/species-data.model';
import { AddSpeciesDialogComponent } from '../../dialogs/add-species-dialog/add-species-dialog.component';
import { Net } from '../../data-models/trip-models/gear-models/net.model';
import { AddNetDialogComponent } from '../../dialogs/add-net-dialog/add-net-dialog.component';
import { TableRules } from '../../data-models/catch-table-models/table-rules.model';
import { DailyCatchReport } from '../../data-models/report-models/daily-catch-report.model';
import { Trip } from '../../data-models/trip-models/trip.model';


@Component({
  selector: 'app-gill-net-report',
  templateUrl: './gill-net-report.component.html',
  styleUrls: ['./gill-net-report.component.css']
})
export class GillNetReportComponent {

  // for updating the table
  @ViewChild(MatTable) table!: MatTable<SpeciesData>;
  responseOptions: string[] = ['Yes', 'No']; //hardcoded values to be used in the form
  fishListDetails: SpeciesData[] = []; // source for table data - all data currently in the table

  // data preambles
  catchReport: DailyCatchReport = new DailyCatchReport(); // catch report we will fill out
  currTrip: Trip = new Trip();
  userDetails: UserData = new UserData('', '');
  unfilteredFishList: SpeciesData[] = []; // temporary array to store species
  fishList: SpeciesData[] = []; //filtered list of species data, will comprise the user's look-up list
  userNets: Net[] = [];
  emptyNet: Net = new Net('', '', '', '', '', '', '', '', '');
  gillNetTable: TableRules = new TableRules(); // used to filter table data

  // values associated with each page
  gillnetForm: UntypedFormGroup = new UntypedFormGroup({

    net: new FormControl<Net>(this.emptyNet, [Validators.required]), //page 1
    hoursFished: new FormControl<string>(''), //page 2

    //page 3
    mgmtArea: new FormControl<string>('', [Validators.required]),
    zoneArea: new FormControl<string>(''),
    numSets: new FormControl<string>(''),

    //page 5
    comments: new FormControl<string>(''), // conditional validation
    nonFish: new FormControl<string>('', [Validators.required]) 
  });

  catchDate: Date = new Date(); //page 2 
  displayedColumns: string[] = []; // page 4


  // section to section
  numPages: number = 0;
  currPage: number = 0;
  spinnerProgress: number = 0;
  spinnerIncrement: number = 0;

  constructor(
    private dialog: MatDialog,
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      this.userDetails = LocalStorageService.getUserByUsername(currUsername);
      this.initialize();
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUser(id, email).then(() => {
                  this.userDetails = LocalStorageService.currUser;
                  this.initialize();
                });
              }
              else {
                this.toastr.error("Please authenticate again", "Cannot get user data");
                this.authenticationService.logout();
              }
            }
            else {
              this.toastr.error("Please authenticate again", "Cannot get user data");
              this.authenticationService.logout();
            }
          });
        }
        else {
          this.toastr.error("Please authenticate again", "Not authenticated");
          this.authenticationService.logout();
        }
      });
    }

  }

  ngOnInit(): void {
    this.onNonFishChange();
  }

  onNonFishChange(): void {
    this.gillnetForm.get('nonFish')?.valueChanges.subscribe((value: string) => {
      const commentControl: AbstractControl<any, any> | null = this.gillnetForm.get('comments');

      if(value === 'Yes'){
        commentControl?.setValidators([Validators.required]);
      }
      else {
        commentControl?.clearValidators();
      }
      commentControl?.updateValueAndValidity();
    })
  }

  // initialize the data
  initialize(): void {

    // get all required data
    this.currTrip = this.userDetails.currTrip;
    this.userNets = this.userDetails.nets;
    this.unfilteredFishList = LocalStorageService.getSpeciesList();
    this.gillNetTable = LocalStorageService.getTableRules('Gill Net');

    this.filterTableData(); // filters table data as well as species list accessible to the user
    this.populateReport(); // populate report with data from previous daily catch report

    // fill catch page details
    let pages: number = 5; 
    this.numPages = pages;
    this.spinnerIncrement = 100 / pages;


    this.displayedColumns = ['speciesName', 'kept', 'released'];

    // go to first screen
    this.toNextPage();
  }

  populateReport(): void {

    // initialize form with net data
    let currNet: Net = this.getSelectedNet();
    this.gillnetForm.patchValue({
      net: currNet
    });

    let numReports: number = this.currTrip.dailyCatchReports.length;
    if (numReports > 0) {
      let prevReport: DailyCatchReport = this.currTrip.dailyCatchReports[numReports-1]; // get last report
      this.gillnetForm.patchValue({ // fill report input fields
        hoursFished: prevReport.hoursFished,
        mgmtArea: prevReport.managementArea,
        zoneArea: prevReport.subArea,
        numSets: prevReport.numSets,
        nonFish: prevReport.nonFish,
        comments: prevReport.comments
      });
    }

  }

  filterTableData(): void {  // filters table data as well as species list accessible to the user
    for(let species of this.unfilteredFishList) {
      if(this.gillNetTable.containsSpecies.includes(species.speciesName)) {
        this.fishListDetails.push(species);
      }
      if(!this.gillNetTable.excludesSpecies.includes(species.speciesName)) {
        this.fishList.push(species);
      }
    }
  }

  determineCellStyling(value: number, name: string = ''): string {

    if(value>0){
      return 'bolded-cell';
    } else if (this.isReleaseOnly(name)) { 
      return 'release-only-cell';
    }

    return 'faded-cell';
  }

  isReleaseOnly(name: string): boolean {
    if(this.gillNetTable.releaseOnlySpecies.includes(name)){
      return true;
    }
    return false;
  }

  // get the currently selected net
  getSelectedNet(): Net {
    for (let net of this.userNets) {
      if (net.isChecked) {
        return net;
      }
    }
    return this.userNets?.length ? this.userNets[0] : this.emptyNet;
  }

  // set the radio buttons to the currently selected net in the catch report
  setSelectedNet(currNet: Net): void {
    for (let net of this.userNets) {
      net.isChecked = (net.netName === currNet.netName);
    }
  }

  // save the selected user nets for UI
  saveUserNets(): void {
    let currNet: Net = this.gillnetForm.get('net')?.value;
    this.setSelectedNet(currNet);
    this.gillnetForm.patchValue({
      net: currNet
    });
  }

  // go to the next page
  toNextPage(): void {

    // page does not contain date
    if (this.currPage <= this.numPages) {
      this.saveUserNets();
      LocalStorageService.saveUser(this.userDetails).then(() => {
        console.log("User data saved to PouchDB");
        this.incrementPage();
      });
    }

    // page is final page
    else {
      this.sendToDFO();
    }

  }

  // go forward a page, changing the current page number and the spinner progress
  incrementPage(): void {
    this.currPage++;
    this.spinnerProgress += this.spinnerIncrement;
  }

  // go back to previous page
  toPreviousPage(): void {

    if (this.currPage === 1) {
      this.saveUserNets();
      LocalStorageService.saveUser(this.userDetails).then(() => {
        console.log("User data saved to PouchDB");
        this.toHome();
      });
    }
    else {
      this.currPage--;
      this.spinnerProgress -= this.spinnerIncrement;
    }

  }

  // add a new net
  addNetDialog(): void {

    const dialogRef: MatDialogRef<AddNetDialogComponent, any> = this.dialog.open(AddNetDialogComponent, {
      width: '500px',
      height: '550px'
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // update selected net as new net added then go to next screen
        if (result !== 'delete') {
          for (let net of this.userNets) { // un-check other nets
            net.isChecked = false;
          }
          let newNet: Net = new Net(result.netType, result.netName, result.netLength, result.netDepth, result.netUnits,
            result.hangRatio, result.meshSize, result.meshUnits, result.numMeshes, result.numStrands, true); 
           this.gillnetForm.patchValue({
             net: newNet
           });
          this.userDetails.nets.push(newNet);

          // updating PouchDB
          LocalStorageService.saveUser(this.userDetails).then(() => {
            console.log("User data saved to PouchDB");
            this.toNextPage();
          });
        }
      }
    });

  }


  // update the specified species catch table
  updateTable(): void {
    this.table.renderRows();
  }

  calculateTotalFish(type: string): number {

    let totalFish = 0;

    if(type === 'kept'){
      for(var i=0; i<this.fishListDetails.length; i++){
        totalFish += this.fishListDetails[i].kept;
      }
    }else if(type === 'released'){
      for(var i=0; i<this.fishListDetails.length; i++){
        totalFish += this.fishListDetails[i].released;
      }
    }
    return totalFish;
  }

  // get new species data from result
  getNewSpecies(result: SpeciesData): SpeciesData {

    let kept: number = result.kept ? +result.kept : 0;
    let released: number = result.released ? +result.released : 0;
    let newSpeciesData: SpeciesData = new SpeciesData(result.speciesName);
    newSpeciesData.kept = kept;
    newSpeciesData.released = released;

    return newSpeciesData;

  }

  // add new species to the catch
  addSpecies(fishListDetails: SpeciesData[]): void {

    const dialogRef: MatDialogRef<AddSpeciesDialogComponent, any> = this.dialog.open(AddSpeciesDialogComponent, {
      width: '500px',
      height: '450px',
      data: { identifier: 'add', species: new SpeciesData(''), currentFishList: fishListDetails, 
      filteredFishList: this.fishList, tableInfo: this.gillNetTable }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // adding a species to the catch
        if (result !== 'delete') {
          fishListDetails.push(this.getNewSpecies(result));
        }

        // update the table
        this.updateTable();
      }
    });

  }

  // edit or delete a species from the catch
  editSpecies(fishListDetails: SpeciesData[], index: number): void {

    const dialogRef: MatDialogRef<AddSpeciesDialogComponent, any> = this.dialog.open(AddSpeciesDialogComponent, {
      width: '500px',
      height: '450px',
      data: { identifier: 'edit', species: fishListDetails[index], currentFishList: fishListDetails, 
      filteredFishList: this.fishList, tableInfo: this.gillNetTable }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result) {

        // editing a species in the catch
        if (result !== 'delete') {
          fishListDetails[index] = this.getNewSpecies(result);
        }

        // delete a species in the catch
        else {
          fishListDetails.splice(index, 1);
        }

        // update the table
        this.updateTable();

      }
    });

  }

  // add to, edit or delete from species catch list
  addSpeciesDialog(index: number = -1): void {

    // editing or deleting a species
    if (index > -1) {
          this.editSpecies(this.fishListDetails, index);
      }

    // adding a species
    else {
          this.addSpecies(this.fishListDetails);
      }

  }

  // check if net value is invalid
  netInvalid(): boolean {
    let net: Net = this.gillnetForm.get('net')?.value;
    return !net || !net.isValid();
  }

  // check if management area is invalid
  managementAreaInvalid(): boolean {
    return this.gillnetForm.get('mgmtArea')?.errors?.required;
  }

  // check if non-fish selection value is invalid
  nonFishInvalid(): boolean {
    return this.gillnetForm.get('nonFish')?.errors?.required;
  }

  // send catch to DFO
  sendToDFO(): void {

    if (this.gillnetForm.valid && !this.netInvalid()) {
      this.saveCatchReport();
      LocalStorageService.saveUser(this.userDetails).then(() => {
        console.log("User data saved to PouchDB");
        this.router.navigate(['submitted-report'], { state: { name: 'Daily Catch Report' } });
      });
    }
    else {
      this.toastr.error("Not all required fields filled", "Invalid form");
    }
  }

  // back to home screen
  toHome(): void {
    this.router.navigate(['active-trip-home']);
  }

  // forbid non-numeric inputs
  forbidNonNumeric(controlName: string): void {
    let control: AbstractControl = this.gillnetForm.get(controlName)!;
    control.setValue(control.value.replace(/[^0-9]/g, ''));
  }

  // only allow one decimal place with 0 or 5 as the tenth digit
  validateHoursFished(controlName: string): void {
    let control: AbstractControl = this.gillnetForm.get(controlName)!;
    const regex: RegExp = /^\d+((\.)|(\.(0|5))?)$/; // matches inputs like: 23, 3, 4.5, 0.5, 9.0, 12., etc.
    if(!regex.test(control.value)){ // if we don't find a match
      control.setValue(control.value.replace(/.$/, '')); // replace the most recently entered character with ''
    }
  }

  // creates a list of all species in the table that have non-zero data associated with them 
  determineSpeciesData(): SpeciesData[] {

    let tempFishArray: SpeciesData[] = [];

    for(let fish of this.fishListDetails) {
      if(fish.kept > 0 || fish.released > 0) {
        tempFishArray.push(fish);
      }
    }
    return tempFishArray;
  }

  // saves all Daily Catch Report information pertinent to Gill Net
  saveCatchReport(): void {

    // save relevant information gathered in the form
    this.catchReport.netUsed = this.gillnetForm.get('net')?.value;
    this.catchReport.dateFished = this.catchDate;
    this.catchReport.hoursFished = this.gillnetForm.get('hoursFished')?.value;
    this.catchReport.managementArea = this.gillnetForm.get('mgmtArea')?.value;
    this.catchReport.subArea = this.gillnetForm.get('zoneArea')?.value;
    this.catchReport.numSets = this.gillnetForm.get('numSets')?.value;
    this.catchReport.nonFish = this.gillnetForm.get('nonFish')?.value;
    this.catchReport.comments = this.gillnetForm.get('comments')?.value;
    this.catchReport.speciesDataList = this.determineSpeciesData();
    this.catchReport.submissionDate = new Date(); // get current date/time

    // push the report to currTrip
    this.currTrip.dailyCatchReports.push(this.catchReport);
    this.userDetails.currTrip = this.currTrip;

  }

}
